import React from 'react'
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import { Col, Container, Row } from 'react-bootstrap';
import CardCustom from '../../components/UI/Card/Card';
const Course = () => {
    return (
        <>
        <Header/>
        <Container fluid>
            <Row>
                <Col><h2>All Courses</h2></Col>
            </Row>
            <Row>
                <Col><CardCustom/></Col>
                <Col><CardCustom/></Col>
                <Col><CardCustom/></Col>
                <Col><CardCustom/></Col>
            </Row>
            <Row>
                <Col><CardCustom/></Col>
                <Col><CardCustom/></Col>
                <Col><CardCustom/></Col>
                <Col><CardCustom/></Col>
            </Row>
        </Container>
        <Footer/>
        </>
    )
}

export default Course
